/* Footer.css */

.footer {
  background-color: #7d0661;
  width: 100%;
  color: #fff;
  padding: 10px 0;
  text-align: center;
  /* position: sticky; */
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-logo {
  flex: 1;
}

.footer-logo img {
  width: 200px;
}

.footer-links {
  flex: 2;
}

.footer-links a {
  color: #fff;
  text-decoration: none;
  margin-right: 20px;
}

.footer-bottom {
  margin-top: 10px;
}


.socials {
  padding: 20px 30px;
  display: flex;
  justify-content: center;
}

.socialIcons {
  display: flex;
  justify-content: space-around;
  /* padding: 10px; */
}

.socialIcons .links {
  padding: 10px;
  text-decoration: none;
  color: #fff;
  font-size: 25px;
}




@media (max-width: 768px) {
  .footer {
    background-color: #7d0661;
    width: 100%;
    color: #fff;
    padding: 10px 0;
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 999;
  }

  .footer-content {
    display: flex;
    justify-content: center;

    align-items: center;
  }

  .footer-logo img {
    width: 130px;
  }

  .footer-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }

}