* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Arial", sans-serif;
  }

  .contactContainer1{
    padding: 30px 40px;
    width: 100%;
    background: #f6f6f6;
    /* height: 60vh; */
  }
  .contactContainer1 .content{
    padding: 10px 20px;
  }
  .contactContainer1 .content h2{
    text-transform: uppercase;
    font-size: 28px;
    font-weight: 500;
  }

  .contactContainer1 .content .form{
    margin-top: 30px;
    background: #fff;
    border-radius: 10px;
    box-shadow: rgb(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 20px 30px;
  }
  .contactContainer1 .content .form form{
    display: flex;
    flex-direction: column;
  }
  .contactContainer1 .content .form form input{
    padding: 10px 30px;
    border: none;
    outline: none;
    background: none;
    border-bottom: 1px solid #000000;
    width: 400px;
    margin-top: 20px;
    margin-left: 0%;
  }

  .contactContainer1 .content .form form textarea{
    padding: 10px 30px;
    border: none;
    outline: none;
    background: none;
    border-bottom: 1px solid #000000;
    max-width: 400px;
    margin-top: 20px;
    margin-left: 0%;
    resize: none;
  }
  .contactContainer1 .content .form form button{
    padding: 10px 0;
    margin-left: 35%;
    width: 100px;
    background: #000;
    outline: none;
    border: none;
    margin-top: 30px;
    color: #fff;
    font-size: 20px;
  }




  @media only screen and (max-width: 768px){
    
  .contactContainer1{
    padding: 10px;
    width: 100%;
    background: #f6f6f6;
    /* height: 60vh; */
  }
  .contactContainer1 .content{
    padding: 10px;
    display: flex;
  }
  .contactContainer1 .content h2{
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 500;
    text-align: center;
  }
  .contactContainer1 .content .form form input{
    padding: 10px 30px;
    border: none;
    outline: none;
    background: none;
    border-bottom: 1px solid #000000;
    width: 230px;
    margin-top: 20px;
    margin-left: 0%;
  }
  .contactContainer1 .content .form form textarea{
    padding: 10px 30px;
    border: none;
    outline: none;
    background: none;
    border-bottom: 1px solid #000000;
    max-width:230px;
    margin-top: 20px;
    margin-left: 0%;
    resize: none;
  }
  }